  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  display: block;
  position: absolute;
  left: -100000px;
  transform-origin: center top;
  width: 100%;
  z-index: 80;
}

.select2-container--open .select2-dropdown {
  top: 1px;
  left: 0;
}

.select2-container--open .select2-dropdown.select2-dropdown--above {
  border-radius: 4px 4px 0 0;
  top: -1px;
}

@keyframes select-menu-scale {
  0% {
    transform: scale(0.87);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.select2-results {
  display: block;
}

.select2-results__options {
  font-size: 1rem;
  list-style: none;
  margin: 0.5rem 0 0 0;
  max-height: 100vh;
  overflow-y: scroll;
  padding: 0 0 0.5rem 0;
  scrollbar-color: #e0dfe1 transparent;
  scrollbar-width: thin;
}

.select2-results__options.select2-results__options--nested {
  margin: 0;
  overflow-y: hidden;
  padding: 0;
}

.select2-results__options.select2-results__options--nested > .select2-results__option {
  padding-left: 1.5rem;
}

.select2-results__option,
.select2-results__group {
  align-items: center;
  display: flex;
  min-height: 3rem;
  padding: 0 1rem;
  position: relative;
}

.select2-results__option {
  letter-spacing: 0.009375em;
  transition: background-color, color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  user-select: none;
}

.select2-results__option--highlighted.select2-results__option--selectable {
  background-color: rgba(0, 0, 0, 0.04);
}

.select2-results__option--highlighted.select2-results__option--selectable:active {
  background-color: rgba(0, 0, 0, 0.24);
}

.select2-results__option--selected {
  background-color: rgba(0, 0, 0, 0.12);
}

.select2-results__option--selected.select2-results__option--highlighted {
  background-color: rgba(0, 0, 0, 0.16);
}

.select2-results__option.select2-results__option--disabled {
  color: rgba(0, 0, 0, 0.38);
}

.select2-results__option--group {
  display: block;
  padding: 0;
}

.select2-results__group {
  color: rgba(0, 0, 0, 0.54);
  font-size: 0.875rem;
}

.select2-results > .select2-results__options {
  overflow-y: auto;
}

.select2-results__option--selectable {
  cursor: pointer;
}

.select2-search--dropdown {
  display: block;
  padding: 5px 4px 0;
}

.select2-search--dropdown::before {
  font-size: 1.51429em;
  opacity: 0.38;
  position: absolute;
  top: 0.6em;
  left: 0.1em;
}

.select2-search--dropdown .select2-search__field {
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.42);
  border-radius: 20px;
  box-sizing: border-box;
  line-height: normal;
  padding: 10px 16px 12px;
  padding-left: 1.5em;
  width: 100%;
}

.select2-search--dropdown .select2-search__field::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

.select2-search--dropdown.select2-search--hide {
  display: none;
}

.select2-close-mask {
  border: 0;
  display: block;
  height: auto;
  margin: 0;
  min-height: 100%;
  min-width: 100%;
  opacity: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: auto;
  z-index: 99;
  background-color: #ffffff;
  filter: alpha(opacity=0);
}

.select2-hidden-accessible {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  white-space: nowrap !important;
  width: 1px !important;
}

.input-group .select2-container {
  flex-grow: 1;
}

.input-group-prepend ~ .select2-container .select2-selection {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.input-group > .select2-container:not(:last-child) .select2-selection {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.select2-container--outlined.select2-container--open .select2-selection {
  border-color: #6200ee;
  box-shadow: inset 2px 2px 0 -1px #6200ee, inset -2px -2px 0 -1px #6200ee;
}

.select2-container--outlined.select2-container--open .select2-selection::before {
  transform: scaleX(0);
}

.select2-container--outlined .select2-selection {
  border: 1px solid rgba(0, 0, 0, 0.42);
  border-radius: 4px;
  line-height: 1.5;
  min-height: 3.5rem;
  padding: 14px 16px 12px;
}

.select2-container--outlined.select2-container--disabled .select2-selection {
